.panel-info {
    .flex();
    justify-content: space-between;
    margin-bottom: 10px;
    
    .panel-info-item {
        .flex();
        .flex-align-items(center);
        background-color: @panel-info-bg;
        .border-radius(@panel-info-border-radius);
        height: @panel-info-height;  
    }
    
    .panel-info-receipts {
        letter-spacing: 0.5px; 
        
        .receipts-item {
            height: 100%;
            border-right: @pi-receipts-item-border;
            .flex();
            flex-flow: column wrap;
            padding-left: @grid-gutter-width / 2;
            padding-right: @grid-gutter-width / 2;
            
            &:last-child {
                border-right: 0;
            }        
            
            .receipts-item--name {
                color: @pi-receipts-item--name-color;
                font-weight: @pi-receipts-item--name-font-weight;
                font-size: @pi-receipts-item--name-font-size;
                padding-top: 8px;
                
                .circle {
                    display: inline-block;
                    width: @pi-receipts-item--name-circle-size;
                    height: @pi-receipts-item--name-circle-size;
                    .border-radius(100%);
                    position: relative;
                    top: -1px;
                    
                    &.green {
                        background-color: @pi-receipts-item--name-circle-green;
                    }
                    
                    &.red {
                        background-color: @pi-receipts-item--name-circle-red;
                    }
                }
            }
            
            .receipts-item--sum {
                font-size: @pi-receipts-item--sum-font-size;
                letter-spacing: 0.4px;
                font-weight: 500;
            }
        }
    } 
    
    .panel-info-empl {    
        .empl-item {
            height: 100%;
            border-right: @pi-empl-item-border;
            padding-left: @grid-gutter-width / 2;
            padding-right: @grid-gutter-width / 2;
            .flex();
            .flex-align-items(center);
            position: relative;
            
            .badge {
                position: absolute;
                top: 5px;
                right: 5px;
                
                background-color: @pi-empl-item-badge-bg;
                color: @pi-empl-item-badge-color;
                border: @pi-empl-item-badge-border;
                
                min-width: @pi-empl-item-badge-minwidth;
                height: @pi-empl-item-badge-height;
                line-height: @pi-empl-item-badge-height - 2px;
                padding: 0 4px;
                
                font-size: @pi-empl-item-badge-font-size;
                font-weight: @pi-empl-item-badge-font-weight;
                .border-radius(100%);
    
                vertical-align: middle;
                text-align: center;
            }
            
            &:last-child {
                border-right: 0;
            }
            
            .fa {
                font-size: 25px;
                width: 30px;
                text-align: center;
            }
        }
    }    
}